<template>
  <div id="withDraw">
    <common-header title="提现"></common-header>
    <div class="withDraw__form" v-show="showForm">
      <Cell title="银行卡" :value="title" @click="linkTo('BankCard')"/>
      <Cell title="支付密码" type="password" v-model="password" placeholder="请输入支付密码"/>
      <Cell class="border" title="金额(￥)" type="number" v-model="price" placeholder="请输入提现的金额"/>
      <div class="form__info">
        <span>当前零钱余额{{accounts.balanceTotal}}元</span>
        <span class="withDraw" @click="takeTotal">全部提现</span>
      </div>
      <div class="form__btn" :class="{'active': isActive}" @click="nextStep">下一步</div>
      <div class="form__tips">
        <div class="tips__title">
          提现说明
        </div>
        <div class="tips__info"></div>
      </div>
    </div>
    
    <!--<template v-show="!showForm">
    	<div id="phone">
	    	<div class="phone__input">
	      	<input type="text" placeholder="请输入收到的验证码" v-model="code">
	      	<span class="btn" :class="{'sended': sended}" @click="getSmsCode" >{{btnText}}</span>
	    	</div>
	    	<div class="phone__info" v-show="sended">验证码已发送至手机：{{mobile}}</div>
	    	<div class="finish__btn" :class="{'active': isActive}" @click="finish">完成</div>
    	</div>
    </template>-->
    
  </div>  
</template>

<script>
  import Cell from '@/components/Cell/Cell';

  export default {
    data() {
      return {
      	title:'选择银行卡',
        price: '',
        password:'',
        accounts: {},
        bankCard:{},
        showForm:true
      }
    },
    computed:{
    	isActive(){
    		if(this.password !='' && this.price != '' && this.price <= this.accounts.balanceTotal && this.accounts.balanceTotal >0 && this.bankCard){
    			return true
    		}else{
    			return false
    		}
    	}
    },
    created() {
			this.getMyAccountList()
			this.getBankCardObj()
    },
    components: {
      Cell
    },
    methods:{
    	getBankCardObj(){
    		if(this.$route.params.bankCard){
    			this.bankCard = this.$route.params.bankCard
    			this.title = this.bankCard.accountBankName
    		}
    	},
    	takeTotal(){
    		if(this.accounts.balanceTotal > 0){
    			this.price = this.accounts.balanceTotal
    		}
    	},
    	getMyAccountList() {
        this.$Api.getMyAccountList(1).then((res) => {
          console.log(res)
          if(res.q.s == 0) {
            this.accounts = res.q.Accounts[0];
          }
        })
     	},
     	nextStep(){
     		let params = {
     			amount:this.price,
     			bankId:this.bankCard.accountBankId,
     			account:this.bankCard.accountIdCode,
     			accountName:this.bankCard.accountName,
     			password:this.password
     		}
     		this.$router.push({name:"Phone",params:{params}})
     	}
    }
    
  }
</script>


<style lang="scss">
  #withDraw {
    .withDraw__form {
      .border {
        border-bottom: 1px solid #eee;
      }

      .form__info {
        height: 1.0667rem;
        line-height: 1.0667rem;
        font-size: 0.373rem;
        padding: 0 0.4rem;
        color: #333;

        .withDraw {
          padding-left: 0.2rem;
          color: #de7fef;
        }
      }

      .form__btn {
        margin: 0.667rem 0.4rem;
        width: calc(100% - 0.8rem);
        height: 1.2rem;
        line-height: 1.1733rem;
        font-size: 0.493rem;
        text-align: center;
        color: #fff;
        border-radius: 1.2rem;
        background: #ccc;
        &.active {
	        background: linear-gradient(135deg, #fe9bba 0%, #de7fef 100%);
		    }
      }
			
      .form__tips {
        padding: 0 0.4rem;
        color: #333;

        .tips__title {
          font-size: 0.4rem;
          padding-bottom: 0.1rem;
          font-weight: 600;
        }
      }
    }
    /*#phone {
	    .phone__input {
	      position: relative;
	      margin: 0 0.4rem;
	      line-height: 1.2rem;
	      height: 1.2rem;
	      border-bottom: 1px solid #eee;
	
	      .btn {
	        position: absolute;
	        top: 0.2rem;
	        right: 0;
	        width: 2.53rem;
	        height: 0.8rem;
	        line-height: 0.8rem;
	        text-align: center;
	        color: #fff;
	        background: linear-gradient(135deg, #fe9bba 0%, #de7fef 100%);
	        border-radius: 0.8rem;
	
	        &.sended {
	          background: #ccc;
	        }
	      }
	    }
	
	    .phone__info {
	      padding: 0 0.4rem;
	      padding-top: 0.3rem;
	      color: #333;
	    }
	  }*/
  }
</style>



